// 主题色
$primary-color: #4477EE;
$success-color: #33BB66;
$danger-color: #FF3344;
$warning-color: #FF9933;
$info-color: #7788AA;

// 背景
$bg-default: #fff;
$bg-gray: #fafafa;
$bg-hover: mix(#fff, $primary-color, 90%);

// 间距
$padding: 5px;
$padding-xs: $padding;  // 5px
$padding-s: $padding*2; // 10px
$padding-m: $padding*3; // 15px
$padding-l: $padding*4; // 20px
$padding-xl: $padding*6; // 30px

// 文字大小
$font-size-sm: 12px;
$font-size-base: 12px;
$font-size-lg: 14px;
$font-size-xl: 16px;
$font-size-heading: 20px;
$font-size-strong: 30px;

// 文字颜色
$color-text-base: #333;            // 基本
$color-text-paragraph: #848587;       // 段落
$color-text-secondary: #c6c7cc;       // 辅助色
$color-text-base-inverse: #fff;    // 基本 - 反色
$color-text-strong: #FF3344;       //价格、强调的数值，包含单位
$color-link: #007AFF;             // 链接

// 线条
$border-color: #E6E9F2;
$dividing-color: #F4F6F8;

// 圆角
$border-radius-base: 4px;
$border-radius-sm: 2px;

// 表格
$panel-bg: #F4F6F8;
$table-header-bg: $panel-bg;

// 宽度
$min-width: 1000px;
$default-width: 90%;

$form-line-height: 32px;

// 表单背景
$input-bg: #F4F6F8;
$input-border-color: #F4F6F8;


// 清除浮动
@mixin clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

@mixin textEllipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}


@mixin HeightOverflowY{
  flex: 1;
  height: 1px;
  overflow-x: hidden;
  overflow-y: auto;
}
.wrap{
  position: relative;
  line-height: normal;
  display: inline-block;
  width: 100%;
  .input{
    :global(.ant-input-group-addon){
      &:after{
        content: ':'
      }
    }
    &:global(.required){
      :global(.ant-input-group-addon){
        &:before{
          content: '*';
          margin-right: $padding-xs;
          color: $color-text-strong;
        }
      }
    }
  }
}

.head{
  padding: $padding-m $padding-l;
  display: flex;
  align-items: center;
  .group{
    flex: 1;
    display: flex !important;
    & + :global(.ant-btn){
      margin-left: $padding-m;
    }
  }
  .select{
    width: 150px;
  }
  .input{
    flex: 1;
  }
}

.dropdown{
  width: 100%;
  min-width: 400px;
  dl,dd,dt,ul,li{
    margin: 0;
    padding: 0;
  }
  :global(.ant-popover-content){
    :global(.ant-popover-arrow){
      display: none;
    }
    :global(.ant-popover-inner-content){
      padding: 0;
    }
  }
  .empty{
    padding: $padding-xl;
  }
  .list{
    padding: 0 $padding-l;
    .column{
      padding: $padding-m 0;
      border-bottom: 1px solid $dividing-color;
      display: flex;
      .dl{
        flex: 1;
        align-items: center;
        .dt{
          color: $color-text-paragraph;
          .name{
            font-size: $font-size-lg;
            color: $color-text-base;
            margin-right: $padding-s;
          }
          & + .dd{
            margin-top: $padding-s;
          }
        }
        .dd{
          .item{
            display: flex;
            align-items: center;
            .itemTit{
              color: $color-text-base;
            }
            .itemCont{
              flex: 1;
            }
            .input{
              width: 200px;
            }
            & + .item{
              margin-top: $padding-xs;
            }
          }
        }
        & + .extra{
          margin-left: $padding-m;
        }
      }
    }
  }
}