// 主题色
$primary-color: #4477EE;
$success-color: #33BB66;
$danger-color: #FF3344;
$warning-color: #FF9933;
$info-color: #7788AA;

// 背景
$bg-default: #fff;
$bg-gray: #fafafa;
$bg-hover: mix(#fff, $primary-color, 90%);

// 间距
$padding: 5px;
$padding-xs: $padding;  // 5px
$padding-s: $padding*2; // 10px
$padding-m: $padding*3; // 15px
$padding-l: $padding*4; // 20px
$padding-xl: $padding*6; // 30px

// 文字大小
$font-size-sm: 12px;
$font-size-base: 12px;
$font-size-lg: 14px;
$font-size-xl: 16px;
$font-size-heading: 20px;
$font-size-strong: 30px;

// 文字颜色
$color-text-base: #333;            // 基本
$color-text-paragraph: #848587;       // 段落
$color-text-secondary: #c6c7cc;       // 辅助色
$color-text-base-inverse: #fff;    // 基本 - 反色
$color-text-strong: #FF3344;       //价格、强调的数值，包含单位
$color-link: #007AFF;             // 链接

// 线条
$border-color: #E6E9F2;
$dividing-color: #F4F6F8;

// 圆角
$border-radius-base: 4px;
$border-radius-sm: 2px;

// 表格
$panel-bg: #F4F6F8;
$table-header-bg: $panel-bg;

// 宽度
$min-width: 1000px;
$default-width: 90%;

$form-line-height: 32px;

// 表单背景
$input-bg: #F4F6F8;
$input-border-color: #F4F6F8;


// 清除浮动
@mixin clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

@mixin textEllipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}


@mixin HeightOverflowY{
  flex: 1;
  height: 1px;
  overflow-x: hidden;
  overflow-y: auto;
}
.drawer{
  padding: 0 !important;
  overflow: hidden !important;
  display: flex;
  flex-direction: row;
  .container{
    flex: 1;
    width: 1px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  .sider{
    width: 250px;
    border-left: 1px solid $border-color;
    overflow-y: auto;
    overflow-x: hidden;
    dl,dt,dd,ul,li{
      margin: 0;
      padding: 0;
    }
    dl{
      padding: $padding-m;
      dt{
        color: $color-text-base;
        margin-bottom: $padding-m;
      }
      dd{
        color: $color-text-paragraph;
        li{
          & + li{
            margin-top: $padding-xs;
          }
        }
      }
      & + dl{
        padding-top: 0;
      }
    }
  }
  .tabs{
    flex: 1;
    display: flex;
    flex-direction: column;
    :global(.ant-tabs-bar){
      padding-left: $padding-l;
      padding-right: $padding-l;
    }
    :global(.ant-tabs-content),
    :global(.ant-tabs-tabpane.ant-tabs-tabpane-active){
      flex: 1;
      display: flex;
      flex-direction: column;
      overflow: hidden;
    }
  }
  .table{
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
    :global(.ant-table.ant-table-small){
      border: none;
      border-radius: 0;
      &>:global(.ant-table-content > .ant-table-body){
        margin: 0;
        :global(.ant-table-thead){
          background: $table-header-bg;
        }
        :global(.ant-table-tbody){
          &>:global(tr.ant-table-row.ant-table-row-level-0){
            &>td:first-child{
              color: $color-text-base;
              font-weight: bold;
            }
          }
          :global(tr.ant-table-row.ant-table-row-level-1){
            & + :global(tr.ant-table-row.ant-table-row-level-0){
              td{
                border-top: 1px solid $border-color;
              }
            }
          }
          &>:global(tr>td){
            border-bottom: none;
          }
        }
      }
    }
  }
}