// 主题色
$primary-color: #4477EE;
$success-color: #33BB66;
$danger-color: #FF3344;
$warning-color: #FF9933;
$info-color: #7788AA;

// 背景
$bg-default: #fff;
$bg-gray: #fafafa;
$bg-hover: mix(#fff, $primary-color, 90%);

// 间距
$padding: 5px;
$padding-xs: $padding;  // 5px
$padding-s: $padding*2; // 10px
$padding-m: $padding*3; // 15px
$padding-l: $padding*4; // 20px
$padding-xl: $padding*6; // 30px

// 文字大小
$font-size-sm: 12px;
$font-size-base: 12px;
$font-size-lg: 14px;
$font-size-xl: 16px;
$font-size-heading: 20px;
$font-size-strong: 30px;

// 文字颜色
$color-text-base: #333;            // 基本
$color-text-paragraph: #848587;       // 段落
$color-text-secondary: #c6c7cc;       // 辅助色
$color-text-base-inverse: #fff;    // 基本 - 反色
$color-text-strong: #FF3344;       //价格、强调的数值，包含单位
$color-link: #007AFF;             // 链接

// 线条
$border-color: #E6E9F2;
$dividing-color: #F4F6F8;

// 圆角
$border-radius-base: 4px;
$border-radius-sm: 2px;

// 表格
$panel-bg: #F4F6F8;
$table-header-bg: $panel-bg;

// 宽度
$min-width: 1000px;
$default-width: 90%;

$form-line-height: 32px;

// 表单背景
$input-bg: #F4F6F8;
$input-border-color: #F4F6F8;


// 清除浮动
@mixin clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

@mixin textEllipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}


@mixin HeightOverflowY{
  flex: 1;
  height: 1px;
  overflow-x: hidden;
  overflow-y: auto;
}
.group{
  display: block;
  line-height: normal;
  .inline{
    display: inline-block;
    min-width: 120px;
    margin-right: $padding-s;
    .col{
      text-align: center;
      padding: $padding-xs $padding-s;
    }
  }
  .col{
    border: 1px solid $border-color;
    display: block;
    padding: $padding-xs $padding-s;
    margin-bottom: $padding-s;
    position: relative;
    border-radius: $border-radius-base;
    background: $bg-default;
    .label{
      font-size: $font-size-base;
      font-weight: normal;
      color: $color-text-base;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      & + .content{
        margin-top: $padding-s;
      }
    }
    .content{
      font-size: $font-size-base;
      color: $color-text-paragraph;
    }
    .tag{
      font-size: $font-size-xl;
      position: absolute;
      z-index: 6;
      right: 0;
      bottom: 0;
      display: none;
    }
    &:global(.ant-radio-wrapper){
      line-height: normal;
      margin: 0 0 $padding-s 0;
      height: auto;
      border: none;
      padding: 0;
      :global(.ant-radio){
        display: none;
        & + span{
          position: relative;
          padding: 0;
          line-height: 1.5;
          display: block;
          border: 1px solid $border-color;
          padding: $padding-xs $padding-s;
          border-radius: $border-radius-base;
          overflow: hidden;
        }
        &:global(.ant-radio-checked){
          & + span{
            border-color: $primary-color;
            box-shadow: inset 0 0 0 1px $primary-color;
            .tag{
              color: $primary-color;
              display: block;
            }
          }
          &:global(.ant-radio-disabled){
            & + span{
              box-shadow: inset 0 0 0 1px $border-color;
              .tag{
                color: $color-text-secondary;
                display: block;
              }
            }
          }
        }
        &:global(.ant-radio-disabled){
          & + span{
            border-color: $border-color;
            background: $bg-gray;
            &,.label{
              color: $color-text-secondary;
            }
          }
        }
      }
    }
    :global(.ant-checkbox){
      display: none;
      & + span{
        padding: 0;
        display: block;
      }
    }
    &:global(.ant-checkbox-wrapper-disabled){
      border-color: $border-color;
      box-shadow: inset 0 0 0 1px $border-color;
      .label,
      .content{
        color: $color-text-secondary;
      }
    }
    &:global(.ant-checkbox-wrapper-checked){
      border-color: $primary-color;
      box-shadow: inset 0 0 0 1px $primary-color;
      .tag{
        color: $primary-color;
        display: block;
      }
      &:global(.ant-checkbox-wrapper-disabled){
        border-color: $border-color;
        box-shadow: inset 0 0 0 1px $border-color;
        .label,
        .content{
          color: $color-text-secondary;
        }
        .tag{
          color: $color-text-secondary;
          display: block;
        }
      }
    }
  }
}