// 主题色
$primary-color: #4477EE;
$success-color: #33BB66;
$danger-color: #FF3344;
$warning-color: #FF9933;
$info-color: #7788AA;

// 背景
$bg-default: #fff;
$bg-gray: #fafafa;
$bg-hover: mix(#fff, $primary-color, 90%);

// 间距
$padding: 5px;
$padding-xs: $padding;  // 5px
$padding-s: $padding*2; // 10px
$padding-m: $padding*3; // 15px
$padding-l: $padding*4; // 20px
$padding-xl: $padding*6; // 30px

// 文字大小
$font-size-sm: 12px;
$font-size-base: 12px;
$font-size-lg: 14px;
$font-size-xl: 16px;
$font-size-heading: 20px;
$font-size-strong: 30px;

// 文字颜色
$color-text-base: #333;            // 基本
$color-text-paragraph: #848587;       // 段落
$color-text-secondary: #c6c7cc;       // 辅助色
$color-text-base-inverse: #fff;    // 基本 - 反色
$color-text-strong: #FF3344;       //价格、强调的数值，包含单位
$color-link: #007AFF;             // 链接

// 线条
$border-color: #E6E9F2;
$dividing-color: #F4F6F8;

// 圆角
$border-radius-base: 4px;
$border-radius-sm: 2px;

// 表格
$panel-bg: #F4F6F8;
$table-header-bg: $panel-bg;

// 宽度
$min-width: 1000px;
$default-width: 90%;

$form-line-height: 32px;

// 表单背景
$input-bg: #F4F6F8;
$input-border-color: #F4F6F8;


// 清除浮动
@mixin clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

@mixin textEllipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}


@mixin HeightOverflowY{
  flex: 1;
  height: 1px;
  overflow-x: hidden;
  overflow-y: auto;
}
.head{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background: #EEEDF1;
  min-width: $min-width;
  height: 65px;
  padding: 0;
  padding-left: $padding-xl;
  display: flex;
  align-items: center;
  .ctrl,
  .info{
    width: 300px;
  }
  .ctrl{
    display: flex;
    align-items: center;
    .logo{
      display: none;
      color: $primary-color;
      font-size: $font-size-heading;
      // & + .btns{
      //   margin-left: $padding-xl;
      // }
    }
    .btns{
      display: flex;
      align-items: center;
      :global(.ant-btn-group + .ant-btn),
      :global(.ant-btn + .ant-btn){
        margin-left: $padding-xs;
      }
      :global(.ant-btn-group){
        :global(.ant-btn + .ant-btn){
          margin-left: -1px;
        }
      }
    }
  }
  .nav{
    flex: 1;
    display: flex;
    justify-content: center;
    .item{
      padding: 0 $padding-s;
      line-height: normal;
      color: $color-text-base;
      :global(.iconfont){
        margin: 0 auto 2px auto;
        display: block;
        text-align: center;
        font-size: 26px;
      }
      .text{
        font-size: $font-size-base;
        text-align: center;
      }
      &:global(.active){
        color: $primary-color;
      }
    }
  }
  .info{
    text-align: right;
    .btn{
      display: none;
      padding: 0 $padding-xl;
      background: none;
      border: none;
      height: 65px;
      line-height: normal;
      outline: none;
      .avatar{
        display: inline-block;
        color: $color-text-base-inverse;
        background:rgba(0,0,0,0.1);
        overflow: hidden;
        vertical-align: middle;
        line-height: 32px;
        :global(.iconfont){
          text-align: center;
          font-size: $font-size-heading;
        }
        img{
          display: block;
        }
        &,img{
          width: 32px;
          height: 32px;
          border-radius: 16px;
        }
      }
      .text{
        display: inline-block;
        vertical-align: middle;
        padding: 0 $padding-s;
        font-size: $font-size-lg;
        text-align: $color-text-base;
        max-width: 120px;
        @include textEllipsis;
      }
    }
  }
}



.toolbar{
  position: fixed;
  right: 0;
  top: 20%;
  z-index: 100;
  .menu{
    .btn{
      width: 32px;
      height: auto;
      white-space: normal;
      display: block;
      font-size: $font-size-base;
      padding: $padding-s 0;
      border-radius: $border-radius-base 0 0 $border-radius-base;
      color: $color-text-base-inverse;
      background: $warning-color;
      border-color: $warning-color;
      :global(.iconfont){
        margin: 0 auto $padding-xs auto;
        display: block;
        font-size: $font-size-xl;
      }
      .span{
        margin: 0 auto;
        line-height: 1.3;
        width: $font-size-base;
        display: block;
        font-size: $font-size-base;
      }
    }
  }
}