// 主题色
$primary-color: #4477EE;
$success-color: #33BB66;
$danger-color: #FF3344;
$warning-color: #FF9933;
$info-color: #7788AA;

// 背景
$bg-default: #fff;
$bg-gray: #fafafa;
$bg-hover: mix(#fff, $primary-color, 90%);

// 间距
$padding: 5px;
$padding-xs: $padding;  // 5px
$padding-s: $padding*2; // 10px
$padding-m: $padding*3; // 15px
$padding-l: $padding*4; // 20px
$padding-xl: $padding*6; // 30px

// 文字大小
$font-size-sm: 12px;
$font-size-base: 12px;
$font-size-lg: 14px;
$font-size-xl: 16px;
$font-size-heading: 20px;
$font-size-strong: 30px;

// 文字颜色
$color-text-base: #333;            // 基本
$color-text-paragraph: #848587;       // 段落
$color-text-secondary: #c6c7cc;       // 辅助色
$color-text-base-inverse: #fff;    // 基本 - 反色
$color-text-strong: #FF3344;       //价格、强调的数值，包含单位
$color-link: #007AFF;             // 链接

// 线条
$border-color: #E6E9F2;
$dividing-color: #F4F6F8;

// 圆角
$border-radius-base: 4px;
$border-radius-sm: 2px;

// 表格
$panel-bg: #F4F6F8;
$table-header-bg: $panel-bg;

// 宽度
$min-width: 1000px;
$default-width: 90%;

$form-line-height: 32px;

// 表单背景
$input-bg: #F4F6F8;
$input-border-color: #F4F6F8;


// 清除浮动
@mixin clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

@mixin textEllipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}


@mixin HeightOverflowY{
  flex: 1;
  height: 1px;
  overflow-x: hidden;
  overflow-y: auto;
}
.wrap{
  margin: 0 auto;
  min-width: $min-width;
  width: $default-width;
  padding-top: 70px + $padding-l;
}

.table{
  :global(.ant-table-tbody){
    & > tr > td{
      border-bottom: none;
    }
    & > tr.parents > td{
      border-top: 1px solid $border-color;
    }
  }
  :global(.ant-table-footer){
    text-align: right;
    padding: 0;
    padding-top: $padding-m;
    background: $bg-default;
    :global(.ant-btn + .ant-btn){
      margin-left: $padding-s;
    }
    &::before{
      background: $border-color;
    }
  }
  .bar{
    color: $color-text-base;
    display: flex;
    align-items: center;
    .barMain{
      flex: 1;
      & + .barCtrl{
        margin-left: $padding-l;
      }
    }
    .barTit,
    .span{
      display: inline;
    }
    .barTit{
      &,:global(.ant-checkbox-wrapper){
        font-weight: bold;
        font-size: $font-size-xl;
      }
      :global(.ant-checkbox){
        padding: 0 12px;
      }
    }
    .span{
      margin-left: $padding-l;
      font-size: $font-size-lg;
    }
  }
}

.container{
  dl,dt,dd{
    margin: 0;
    padding: 0;
  }
  .order{
    background: $bg-default;
    padding: $padding-l;
    border: 1px solid $border-color;
    dl{
      dt{
        display: flex;
        align-items: center;
        .tit{
          font-size: $font-size-xl;
          font-weight: bold;
          color: $color-text-base;
          flex: 1;
          width: 1px;
        }
        & + dd{
          margin-top: $padding-m;
        }
      }
      & + dl{
        margin-top: $padding-l;
      }
    }
    & + .column{
      margin-top: $padding-l;
    }
    & + .total{
      margin-top: $padding-l;
    }
  }
  .column{
    background: $bg-gray;
    padding: $padding-l;
    background: $bg-default;
    border: 1px solid $border-color;
    dl{
      padding-bottom: $padding-l;
      display: flex;
      align-items: center;
      & + dl{
        border-top: 1px solid $border-color;
        padding-top: $padding-l;
      }
      &:last-child{
        padding-bottom: 0;
      }
      dt{
        font-size: $font-size-xl;
        font-weight: bold;
        color: $color-text-base;
        width: 100px;
        & + .dd{
          margin-left: $padding-l;
        }
      }
      .dd{
        flex: 1;
        & + .ft{
          margin-left: $padding-l;
        }
        .span{
          display: inline;
          margin-right: $padding-xl;
        }
        .total{
          text-align: right;
          font-size: $font-size-lg;
          font-weight: bold;
          color: $color-text-base;
        }
      }
      .ft{
        span + a,
        span + :global(.ant-btn),
        a + :global(.ant-btn),
        :global(.ant-btn + .ant-btn),
        :global(.ant-btn + a){
          margin-left: $padding-xl;
        }
      }
    }
    & + .total{
      margin-top: $padding-l;
    }
  }
}

.footer{
  padding: $padding-l 0;
  text-align: right;
  :global(.ant-btn + .ant-btn){
    margin-left: $padding-s;
  }
}