// 主题色
$primary-color: #4477EE;
$success-color: #33BB66;
$danger-color: #FF3344;
$warning-color: #FF9933;
$info-color: #7788AA;

// 背景
$bg-default: #fff;
$bg-gray: #fafafa;
$bg-hover: mix(#fff, $primary-color, 90%);

// 间距
$padding: 5px;
$padding-xs: $padding;  // 5px
$padding-s: $padding*2; // 10px
$padding-m: $padding*3; // 15px
$padding-l: $padding*4; // 20px
$padding-xl: $padding*6; // 30px

// 文字大小
$font-size-sm: 12px;
$font-size-base: 12px;
$font-size-lg: 14px;
$font-size-xl: 16px;
$font-size-heading: 20px;
$font-size-strong: 30px;

// 文字颜色
$color-text-base: #333;            // 基本
$color-text-paragraph: #848587;       // 段落
$color-text-secondary: #c6c7cc;       // 辅助色
$color-text-base-inverse: #fff;    // 基本 - 反色
$color-text-strong: #FF3344;       //价格、强调的数值，包含单位
$color-link: #007AFF;             // 链接

// 线条
$border-color: #E6E9F2;
$dividing-color: #F4F6F8;

// 圆角
$border-radius-base: 4px;
$border-radius-sm: 2px;

// 表格
$panel-bg: #F4F6F8;
$table-header-bg: $panel-bg;

// 宽度
$min-width: 1000px;
$default-width: 90%;

$form-line-height: 32px;

// 表单背景
$input-bg: #F4F6F8;
$input-border-color: #F4F6F8;


// 清除浮动
@mixin clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

@mixin textEllipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}


@mixin HeightOverflowY{
  flex: 1;
  height: 1px;
  overflow-x: hidden;
  overflow-y: auto;
}
.main{
  dl,dt,dd{
    margin: 0;
    padding: 0;
  }
  .dl{
    margin-bottom: $padding-l;
    .dt{
      font-size: $font-size-lg;
      font-weight: bold;
      color: $color-text-base;
      & + .dd{
        margin-top: $padding-m;
      }
    }
    .group{
      display: block;
      width: 100%;
      .item{
        padding: 0 $padding-l;
        display: flex;
        align-items: center;
        .itemCont{
          flex: 1;
          width: 1px;
          @include textEllipsis;
          :global(.ant-checkbox-wrapper + span),
          :global(.ant-checkbox + span){
            padding-left: $padding-m;
          }
          & + .itemCtrl{
            margin-left: $padding-s;
          }
        }
        .itemCtrl{
          :global(.ant-btn){
            font-size: $font-size-lg;
            padding: 0;
            height: auto;
            background: none;
            border: none;
          }
        }
        & + .item{
          margin-top: $padding-s;
        }
      }
      .column{
        padding: $padding-m $padding-l;
        display: flex;
        background: $bg-gray;
        margin-bottom: $padding-m;
        p{
          margin: 0;
          padding: 0;
        }
        .checkbox{
          margin-top: 3px;
          & + .colMain{
            margin-left: $padding-m;
          }
        }
        .colMain{
          flex: 1;
          .colHead{
            display: flex;
            font-size: $font-size-lg;
            font-weight: bold;
            color: $color-text-base;
            & + .colBody{
              margin-top: $padding-s;
            }
          }
          .colBody{
            color: $color-text-paragraph;
            .section{
              & + .section{
                margin-top: $padding-xs;
              }
            }
          }
          & + .colExtra{
            margin-left: $padding-m;
          }
        }
        .colExtra{
          .strong{
            color: $color-text-strong;
            font-size: $font-size-lg;
          }
          .section{
            & + .section{
              margin-top: $padding-xs;
            }
          }
        }
      }
    }
  }
}

.footer{
  display: flex;
  .ftMain{
    text-align: left;
    flex: 1;
    & + .ftExtra{
      margin-left: $padding-l;
    }
  }
}